.gpt8__features{
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: row;

}
.gpt8__features-heading{
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;

    text-align: left;
    margin-right: 5rem;
}
.gpt8__features-heading h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
}
.gpt8__features-heading p{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: var(--color-subtext);
    margin-top: 2rem;

}
.gpt8__features-container{
    flex: 1.5;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
}


@media screen and (max-width:990px) {
    .gpt8__features{
        flex-direction: column;
    }
    .gpt8__features-heading{
        margin: 0 0 2rem 0;
    }
    
}
@media screen and (max-width:550px) {
    .gpt8__features-heading h1{
        font-size: 28px;
        line-height: 38px;
    }
    
    
}

