.gpt8__blog{
    display: flex;
    flex-direction: column;

}

.gpt8__blog-heading{
    width: 100%;
    text-align: left;
    margin-bottom: 5rem;
}
.gpt8__blog-heading h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    letter-spacing: -0.04em;
}
.gpt8__blog-container{
    display: flex;
    flex-direction: row;

}
.gpt8__blog-container_groupA{
    flex: 0.75;
    margin-right: 2rem;
    
}
.gpt8__blog-container_groupB{
    flex: 1;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: 2rem;
    
}

@media screen and (max-width:990px) {
    .gpt8__blog-container{
        flex-direction: column-reverse;
    }
    .gpt8__blog-container_groupA{
        margin: 2rem 0;
    }
    .gpt8__blog-container_groupA .gpt8__blog-container_article{
        width: 48%;
    }
    .gpt8__blog-container_groupA .gpt8__blog-container_article-image{
        height: 250px;
    }

}

@media screen and (max-width:700px) {

    .gpt8__blog-container_groupB{
        grid-template-columns: repeat(1,1fr);
    }
    .gpt8__blog-container_groupA .gpt8__blog-container_article{
        width: 100%;
    }
    .gpt8__blog-heading h1{
        line-height: 52px;
        font-size: 46px;
    }

}
@media screen and (max-width:550px) {

    .gpt8__blog-heading h1{
        line-height: 42px;
        font-size: 34px;
    }

}