.gpt8__cta{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    padding: 2rem;
    margin: 4rem;
    border-radius: 1rem;
    background: var(--gradient-bar);
}
.gpt8__cta-content p{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 12px;
    line-height: 30px;
    color: #0E0E0E;
}
.gpt8__cta-content h3{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 24px;
    line-height: 45px;
    color: #000;
}
.gpt8__cta-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 2rem;

}
.gpt8__cta-btn button{
    border-radius: 40px;
    background: #000;
    color: #fff;
    font-family: var(--font-family);
    font-weight: bold;
    font-size: 18px;
    line-height: 30px;
    border: none;
    padding: 0.5rem 1rem;outline: none;
    cursor: pointer;
    min-width: 150;
}

@media screen and (max-width:650px) {
    .gpt8__cta{
        flex-direction: column;
    }
    .gpt8__cta-btn{
        margin: 2rem 0 0;
    }

}
@media screen and (max-width:550px) {
    .gpt8__cta{
        margin: 4rem 2rem;
    }
    .gpt8__cta-content h3{
        font-size: 18px;
        line-height: 32px;
    }
    .gpt8__cta-btn button{
        font-size: 14px;
        line-height: 28px;
    }
    
}